@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900&display=swap&subset=cyrillic,cyrillic-ext);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Roboto', sans-serif;overflow-x:hidden}body{font-size:14px}ul,ol{list-style:none}a{text-decoration:none}img{display:block;width:100%;height:100%}.main-container{max-width:1440px;display:flex;align-items:flex-start;width:100%;margin:auto;background:rgba(227,227,227,0.329412)}

.content-page{padding:30px;width:100%;max-width:calc(1440px - 300px)}

.list-item{width:100%}.list-item--head{width:100%;display:flex}.list-item--body{padding:0 15px}.list-item--body p{font-weight:300;font-size:16px;margin:15px 0;line-height:24px}.list-item--body .head{border-bottom:2px solid #36a4dc;display:block;padding-bottom:10px;margin-bottom:15p;width:-webkit-max-content;width:max-content;font-size:22px;font-weight:400}.list-item--body .block{border-bottom:1px solid #e3e3e3;padding-bottom:10px;margin:15px 0}.list-item--body ul{font-size:16px}.list-item--body ul div{font-size:16px;margin-bottom:10px}.list-item--body li{font-weight:300;font-size:16px;margin:5px 0}.list-item--body a{font-weight:500;color:#333;text-decoration:underline}.list-item--body b{font-weight:500;color:#333}

.content-block{width:100%;padding:15px;box-sizing:border-box;margin-bottom:30px;background:#fff;border-radius:10px;box-shadow:0 0 4px 2px #23151526;border:1px solid rgba(227,227,227,0.329412)}.content-block--head{display:flex;align-items:center}.content-block--head svg{width:30px;height:30px;fill:rgba(227,227,227,0.329412)}.content-block--title{width:calc(100% - 55px);padding:5px 15px 15px 0;margin-left:15px;border-bottom:1px solid rgba(227,227,227,0.329412);font-size:25px;font-weight:300;color:#333}

.sidebar{background:#36a4dc;color:#fff;width:300px;min-height:100vh;box-shadow:0 0 4px 2px #23151526;position:relative;margin-left:30px;margin-top:30px;border-radius:10px}.sidebar--info{position:absolute;width:80%;padding:10px;background:rgba(0,0,0,0.5);top:200px}.sidebar--name{font-size:22px;font-weight:600;color:#fff}.sidebar--prof{width:-webkit-min-content;width:min-content;white-space:nowrap;font-size:18px;color:#fff;font-weight:300;margin-top:5px;padding-top:5px;border-top:1px solid #36a4dc}.contact-block{width:100%;padding:15px;margin-top:10px}.contact-block--head{display:flex;align-items:center;margin-bottom:10px}.contact-block--icon{width:20px;height:20px;fill:#fff}.contact-block--title{color:#fff;font-weight:500;font-size:20px;margin-left:8px}.contact-block--text{font-size:13px;line-height:22px}.contact-block--link{display:block;width:100%;color:#fff;font-size:15px;line-height:22px;text-decoration:none;font-weight:300}

